.button {
  position: relative;
  display: inline-block;
  background-color: #408F95;
  padding: 0.9375rem 5.5rem 1rem 2.5rem;
  border: 1px solid #408F95;
  color: #fff;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  z-index: 1;
  margin-bottom: 0.25rem;
}

.button::before {
  content: "";
  position: absolute;
  z-index: -2;
  transition: width 0.3s ease;
  top: 0.25rem;
  left: 0.25rem;
  width: 100%;
  height: 100%;
  border: 1px solid #408F95;
}

.button::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 0;
  display: block;
  background: #fff;
  transition: 0.3s;
  left: 0;
  z-index: -1;
}

.button:hover::after {
  width: 100%;
}

.button span::after {
  content: "";
  position: absolute;
  margin-left: 0.5rem;
  top: 50%;
  transform: translateY(-50%);
  width: 2.5rem;
  height: 0.375rem;
  background-image: url(../images/common/arrow-icon.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transition: background-image 0.3s ease;
}

.button:hover {
  color: #408F95;
}

.button:hover span::after {
  background-image: url(../images/common/arrow-green.svg);
}

.button--not-found {
  background-color: #fff;
  color: #408F95;
}

.button--not-found::before {
  border: 1px solid #fff;
  z-index: 0;
}

.button--not-found::after {
  background: #408F95;
}

.button--not-found span::after {
  background-image: url(../images/common/arrow-green.svg);
}

.button--not-found:hover {
  color: #fff;
}

.button--not-found:hover span::after {
  background-image: url(../images/common/arrow-icon.svg);
}

